const Theme = {
    LIGHT: 'light-theme',
    DARK: 'dark-theme',
};

const refs = {
    block: document.querySelector('#js-body'),
    switcher: document.querySelector('.js-switch-input'),
};

const savedSwitcher = localStorage.getItem('switchere');
console.log(typeof savedSwitcher);

if (Number(savedSwitcher)) {
    refs.block.classList.add(`${Theme.DARK}`);
    refs.switcher.checked = 1;
} else {
    refs.block.classList.add(`${Theme.LIGHT}`);
    refs.switcher.checked = 0;
}

refs.switcher.addEventListener('click', function () {
    if (refs.block.classList.contains(`${Theme.DARK}`)) {
        localStorage.setItem('switchere', (refs.switcher.checked = 0));
        refs.block.classList.remove(`${Theme.DARK}`);
        refs.block.classList.add(`${Theme.LIGHT}`);
        refs.switcher.checked = 0;
    } else {
        localStorage.setItem('switchere', (refs.switcher.checked = 1));
        refs.block.classList.remove(`${Theme.LIGHT}`);
        refs.block.classList.add(`${Theme.DARK}`);
        refs.switcher.checked = 1;
    }
});