import menu from '../menu.json';
import menuFeedItemTemplate from '../templates/post-feed-item.hbs';
import postFeedTemplate from '../templates/post-feed.hbs';
import '../css/styles.css';

// console.log(postFeedItemTemplate);
// console.log(posts);

const refs = {
  menuFeed: document.querySelector('.js-menu'),
};

buildMenuFeed(menu);

function buildMenuFeed(menu) {
  const markup = menu.map(menu => menuFeedItemTemplate(menu)).join('');
  refs.menuFeed.insertAdjacentHTML('beforeend', markup);
}